.words {
    display: flex;
    flex-direction: column;
    gap: 20px;

    margin-bottom: 80px;

    @media (max-width: $md2) {
        margin-bottom: 40px;
        gap: 10px;
    }

    &__content {
        display: flex;
        justify-content: space-between;
        align-items: center;

        border-bottom: 1px solid black;
        padding-bottom: 20px;

        @media (max-width: $md2) {
            padding-bottom: 10px;
        }
    }

    &__main {
        display: flex;
        flex-direction: column;
        gap: 10px;

        @media (max-width: $md2) {
            gap: 5px;
        }

        @media (max-width: $md2) {
            h2 {
                font-size: 23px;
            }
            h3 {
                font-size: 15px;
            }
        }
    }
    &__right {
        display: flex;
        align-items: center;
        gap: 30px;
        padding-left: 20px;

        @media (max-width: $md2) {
            gap: 5px;
            flex-direction: column;
            align-items: flex-start;
            padding: 5px 0;
        }
    }
    &__count-learned {
        display: flex;
        align-items: center;
        gap: 10px;

        @media (max-width: $md2) {
            gap: 0;
        }
    }

    &__title {
        text-align: center;
        font-size: 35px;

        margin: 30px 0;

        @media (max-width: $md2) {
            font-size: 30px;
        }
    }
    &__no-words {
        font-size: 20px;

        display: flex;
        align-items: center;
        justify-content: center;
        gap: 10px;

        margin: 100px 0;
    }
    button {
        @media (max-width: $md2) {
            font-size: 13px;
        }
    }
    &__header-btns {
        display: flex;
        align-items: center;

        gap: 20px;

        margin-bottom: 30px;
    }
}
