.nav-header {
    // .nav-header__list
    &__list {
        display: flex;
        align-items: center;
        gap: 24px;
    }
    // .nav-header__item
    &__item {
    }
    // .nav-header__link
    &__link {
        color: #1f1f1f;
        font-size: 20px;
        font-weight: 400;
        line-height: 120%; /* 19.2px */
        cursor: pointer;

        transition: 0.25s ease-in-out all;

        @media (any-hover: hover) {
            &:hover {
                color: $blue;
            }
        }
    }
}
