.lernen {
    // .lernen__content
    &__content {
    }
    // .lernen__title
    &__title {
        margin-bottom: 30px;
        text-align: center;
    }

    // .lernen__body
    &__body {
        display: flex;
        flex-direction: column;
        align-items: center;
    }
    // .lernen__word
    &__word {
        margin: 80px 0;
        font-size: 30px;
        font-weight: 500;
        text-align: center;
    }
    &__count-word {
        margin-top: 10px;
        font-weight: 500;

        span {
            font-weight: 400;
        }
    }
    // .lernen__translates
    &__translates {
        display: flex;
        flex-direction: column;
        align-items: center;
        gap: 15px;
        max-width: 300px;
        width: 100%;
    }
    &__button {
        width: 100%;
    }
    &__answer-button {
        transition: 0.3s ease-in-out all;

        padding: 10px 3px !important;

        &.wrong {
            background-color: #8e2424 !important;
        }
        &.correct {
            background-color: #119529 !important;
        }
    }
    &__finisch {
        margin-top: 50px;
        text-align: center;
    }
    &__finished {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        gap: 15px;

        &-group {
            display: flex;
            justify-content: center;
            align-items: center;
            gap: 20px;

            @media (max-width: $md2) {
                margin-top: 50px;
            }
        }
    }
    &__finished-title {
        text-align: center;
        font-weight: 400;
        line-height: 130%;

        margin: 30px 0;

        span {
            color: $blue;
            font-weight: 500;
        }

        @media (max-width: $md2) {
            margin-top: 50px;
            font-size: 20px;
        }
    }
    &__finished-nav {
        color: white;
        font-size: 15px;
        padding: 5px 10px;
    }
}
