@import "fonts";
@import "libs/normalize";
@import "libs/vars";

body {
    background-color: #fff;
    font-family: "Roboto";
    color: #111827;
}

._container {
    max-width: 1400px;
    padding: 0px 15px;
    margin: 0 auto;
}

.main {
    min-height: 60vh;
    @media (max-width: $md1) {
        margin-top: 100px;
    }
}

._container-footer {
    max-width: 1400px;
    padding: 0px 15px;
    margin: 0 auto;
}

#root {
    scrollbar-width: none;
}

body::-webkit-scrollbar {
    width: 0; /* Для Chrome, Safari и Opera */
}

// UI
@import "components/UI/underline-anim";

// components
@import "components/header";
@import "components/footer";
@import "components/nav-header";
@import "components/burger";
@import "components/add-word";
@import "components/words";
@import "components/lernen";
@import "components/modal";
