.add-word {
    max-width: 900px;

    margin: 0 auto;
    &__content {
        display: flex;
        flex-direction: column;
        align-items: center;
        width: 100%;
        gap: 30px;
    }
    &__title {
        font-size: 22px;
        text-align: center;
    }
    &__text {
        color: #0cc738;
        font-size: 19px;
    }
}
