.footer {
    margin-bottom: 50px;
    margin-top: 50px;
    &__content {
        display: flex;
        align-items: center;
        justify-content: space-between;

        a {
            text-decoration: underline;
            color: #2c2626;
        }

        @media (max-width: $md2) {
            flex-direction: column;
            gap: 30px;
            justify-content: center;
        }
    }
    &__logo {
        width: 60px;
        height: auto;
    }
    &__beta {
        max-width: 600px;
        width: 100%;
        text-align: center;
        font-size: 12px;
        display: flex;
        align-items: center;
        gap: 10px;
        img {
            width: 15px;
            height: auto;
        }
    }
}
