.modal {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;

    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 85%;
    background-color: #fff;
    border-radius: 5px;
    box-shadow: 24px;

    padding-bottom: 20px;

    &__header {
        align-self: flex-end;

        margin-top: 5px;
        margin-right: 5px;
    }

    &__body {
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
        width: 100%;

        gap: 20px;

        margin: 20px 0;
        margin-top: 0;
    }

    &__delete {
        padding-left: 20px;
    }

    &__group-btn {
        display: flex;
        align-self: flex-start;
        gap: 20px;
    }
}
